import DashboardOutlinedIcon from '@mui/icons-material/DashboardOutlined';
import GroupsOutlinedIcon from '@mui/icons-material/GroupsOutlined';
import { Icon } from "./icons";

export const Navbar = ({ dashboardPage, publisherPage }) => {
  return (
    <>
      <div
        className="fixed 
      inset-x-0 bottom-0 flex h-12 
      items-center justify-around bg-background
      md:h-screen md:w-12 md:flex-col md:justify-start"
      >
        <Icon label="Home" action={dashboardPage}>
        <DashboardOutlinedIcon/>
        </Icon>
        <Icon label="Publishers" action={publisherPage}>
          <GroupsOutlinedIcon />
        </Icon>
      </div>
    </>
  );
};
