export const Content = ({ children }) => {
  return (
    <div
      className="fixed 
      inset-y-12 
      inset-x-0 
      overflow-auto 
      overscroll-contain 
      md:left-12 
      md:right-0
      md:bottom-0"
    >
      <div className="fixed h-screen w-screen -z-10 bg-primary"></div>
      {children}
    </div>
  );
};
